<template>
  <div id="app">
    <render ref="render" v-on:full="full" v-on:back="back" v-on:refresh="getcode" :msg="render_msg"></render>
    <edtior ref="editor"></edtior>
  </div>
</template>

<script>
import render from "./components/render.vue";
import block from "./components/block.vue";
/*function datas(h = "", u = "") {
  var res = h + u + "\n";
  return res;
}
function data_load(u = "", h) {
  return datas(h, u);
}*/

export default {
  name: "App",
  components: {
    render,
    edtior: block
  },
  data() {
    return {
      render_msg: '',
      header_html_head: "\n<html>\n",
      header_html_end: "\n</html>\n",
      header_lib_end: "\n</head>\n",
      header_content_head: "\n<body>\n<script>\n",
      header_content_end: "\n</" + "script>\n</body>\n",
      header_lib_head: "\n<head>\n",
      header:
        "<link rel='stylesheet' href='https://unpkg.com/element-ui/lib/theme-chalk/index.css'>\n",
      io: "",
      sheader:
        "<script src='https://unpkg.com/element-ui/lib/index.js'></" +
        "script>\n<script src='https://cdn.jsdelivr.net/npm/vue/dist/vue.js'></" +
        "script>"
    };
  },
  methods: {
    hello() {
      alert("hello");
    },
    parse(io) {
      this.render_msg=''
      this.$refs.render.load()
      var __this = this;
      var headers =
        this.header_html_head +
        this.header_lib_head +
        this.header +
        this.sheader +
        this.header_lib_end +
        this.header_content_head +
        io +
        this.header_content_end +
        this.header_html_end;
      console.log(headers);
      setTimeout(() => {

          __this.render_msg = headers;
          this.$refs.render.ok()
        }, 500);
      
      console.log(__this.render_msg);
      console.log("finish");
      this.$refs.render._data.loading = false;
      console.log(this.$refs.render);
    },
    full() {
      var d = document.getElementById("render");
      d.style.height = "100%";
      d.style.width = "100%";
      d.style.top = "0%";
    },
    back() {
      var d = document.getElementById("render");
      d.style.height = "35%";
      d.style.width = "35%";
      d.style.top = "5%";
    },
    getcode() {
      console.log("getcode started");
      console.log("getcode sent");
      var result = this.$refs.editor.run();
      console.log("getcode ok");
      this.parse(result);
    }
  }
};
</script>

