<template>
  <div id="editor" style="box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)">
    <!-- xml ='https://developers.google.com/blockly/xml' id='toolbox' style='display: none'>
    <category name='Logic' colour='%{BKY_LOGIC_HUE}'>
      <block type='controls_if'></block>
      <block type='logic_compare'></block>
      <block type='logic_operation'></block>
      <block type='logic_negate'></block>
      <block type='logic_boolean'></block>
    </category>
    <category name='Loops' colour='%{BKY_LOOPS_HUE}'>
      <block type='controls_repeat_ext'>
        <value name='TIMES'>
          <block type='math_number'>
            <field name='NUM'>10</field>
          </block>
        </value>
      </block>
      <block type='controls_whileUntil'></block>
    </category>
    <category name='Math' colour='%{BKY_MATH_HUE}'>
      <block type='math_number'>
        <field name='NUM'>123</field>
      </block>
      <block type='math_arithmetic'></block>
      <block type='math_single'></block>
    </category>
    <category name='Text' colour='%{BKY_TEXTS_HUE}'>
      <block type='text'></block>
      <block type='text_length'></block>
      <block type='text_print'></block>
    </category>
    </xml-->
    <div id="ed" style="box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)"></div>
  </div>
</template>
<script>
import xmlparse from "vkbeautify";
import ed from "blockly";
function test(html) {
      window.media_delay = 0;
      var c = self.setInterval("window.media_delay=window.media_delay+1;", 0);
      var img = new Image();
      img.onreadystatechange = function () {
        window.clearInterval(c);
        if (img.readyState == 'complete' || img.readyState == 'loaded') {
          window.media_delay = window.media_delay + 0
        }
        else {
          window.media_delay = 99999999999999999999999999999999999999999999999999999999999999
        }
      }
      img.onerror = function () {
        window.clearInterval(c);
        window.media_delay = 99999999999999999999999999999999999999999999999999999999999999
      }
      img.src = html + 'quote0.png';
      return window.media_delay;
    }
    function chose() {
      var testlist = ['https://yueiuieui.github.io/', 'https://yueiuieui.gitee.io/metalscratchsrc/']
      var data = {}
      var i = 0
      for (i in testlist) {
        data[testlist[i]] = test(testlist[i])
      }
      var items = Object.keys(data).map(function (key) {
        return [key, data[key]];
      });

      // Sort the array based on the second element
      items.sort(function (first, second) {
        return second[1] - first[1];
      });
      
      return items[0][0]
    }
export default {
  data() {
    return {
      cursor: null,
      parse: xmlparse,
      xml: null,
      d: ""
    };
  },
  mounted() {
    this.$http.get(chose()+'/blocks.xml').then(function(response){
    // 响应成功回调
      this.d=response.body
      console.log(response.body)
      var s = ed.inject("editor", { toolbox: this.d });

    ed.Xml.domToWorkspace(document.getElementById("ed"), s);

    this.cursor = s;
    this.$on("getcode", e => {
      console.log(e);
      console.log("generate code 1");
      this.run();
    });
    }, function(response){
    // 响应错误回调
    console.log(response)
    
    });
    
  },
  methods: {
    run() {
      // Generate JavaScript code and run it.
      console.log("generate code");
      window.LoopTrap = 1000;
      ed.JavaScript.INFINITE_LOOP_TRAP =
        'if (--window.LoopTrap == 0) throw "Infinite loop.";\n';
      var code = ed.JavaScript.workspaceToCode(this.cursor);
      ed.JavaScript.INFINITE_LOOP_TRAP = null;
      console.log("generate code ok");

      return code;
    }
  }
};
</script>
